@tailwind base;
@tailwind components;
@tailwind utilities;

@layer {
    .container {
        @apply px-[30px] mx-auto
    }
    .element-center {
        @apply flex items-center justify-center
    }
    .btn {
        @apply bg-[#42b0d1] hover:bg-[#8cdae4] transition-all duration-300
    }
}

body {
    font-family: 'Roboto', sans-serif;
    background-color: #181e2a;
    height: 500vh;
}
